<template>
  <div>
    <WorkerPopupAdd
      :popupHidden="popupAddHidden"
      @add-worker="addWorker"
      @popup-toggle="popupAddToggle"
    />

    <AppTopPanel header="–°–ø–∏—Å–æ–∫ —Ä–∞–±–æ—á–∏—Ö">
      <template #nav-buttons>
        <AppButtonIcon icon="autorenew" title="–û–±–Ω–æ–≤–∏—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—É" @button-click="updateWorkers()"/>
        <AppButtonIcon icon="search" title="–ü–æ–∏—Å–∫" @button-click="$router.push('/workers/search')"/>
        <AppButtonIcon icon="add" title="–î–æ–±–∞–≤–∏—Ç—å —Ä–∞–±–æ—á–µ–≥–æ" @button-click="popupAddToggle"/>
      </template>
    </AppTopPanel>

    <section>
      <TableWorkers
        v-if="workers"
        @remove-worker="removeWorker"
        :workers="workers"
      />
    </section>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import firebase from 'firebase/app'

import AppTopPanel from '@/components/AppTopPanel'
import AppButtonIcon from '@/components/AppButtonIcon'
import TableWorkers from '@/components/workers/WorkersTable'
import WorkerPopupAdd from '@/components/workers/WorkerPopupAdd'

export default {
  name: 'Workers',

  components: {
    AppTopPanel,
    AppButtonIcon,
    TableWorkers,
    WorkerPopupAdd
  },

  data () {
    return {
      popupAddHidden: true
    }
  },

  computed: {
    ...mapGetters([
      'workers'
    ])
  },

  created () {
    this.SET_WORKERS_FROM_LOCAL_STORAGE()
  },

  methods: {
    ...mapMutations([
      'SET_WORKERS_FROM_SERVER',
      'SET_WORKERS_FROM_LOCAL_STORAGE'
    ]),

    popupAddToggle () {
      this.popupAddHidden = !this.popupAddHidden
    },

    removeWorker (id) {
      firebase.database().ref('/workers/' + id).remove()
        .then(() => {
          this.SET_WORKERS_FROM_SERVER()
          console.log('–†–∞–±–æ—á–∏–π —É–¥–∞–ª—ë–Ω üóëÔ∏è')
        })
    },

    addWorker (worker) {
      firebase.database().ref('/workers/' + worker.id).set(worker)
        .then(() => {
          this.SET_WORKERS_FROM_SERVER()
          console.log('–†–∞–±–æ—á–∏–π –¥–æ–±–∞–≤–ª–µ–Ω ‚ûï')
        })
    },

    updateWorkers () {
      this.SET_WORKERS_FROM_SERVER()
      // eslint-disable-next-line no-undef
      M.toast({ html: '–°–æ—Ç—Ä—É–¥–Ω–∏–∫–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã' })
    }
  }
}
</script>
